@import "variables.scss";

.header {
  max-width: 1920px;

  &-container {
    margin: 0 auto;
    max-width: 1170px;
    padding: 33px 0 25px 0;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__top-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__notion {
    font-weight: 400;
    max-width: 300px;
    font-size: 14px;
    @media (max-width: 1170px) {
      width: 100%;
    }

    @media (max-width: 930px) {
      max-width: 180px;
    }
  }

  &__bottom-content {
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
}